.background-svg-container {
  position: fixed;
  width: 100vw; /* Set width and height to occupy full viewport */
  height: 100vh;
}

.background-svg-container svg {
  position: absolute;
  width: 100%;
  height: 100%;
}

.upper-right {
  top: 0;
  right: 0;
}

.bottom-left {
  bottom: 0;
  left: 0;
}
